<template>
	<view class="container">
		<view class="goods-img">
			<image :src="goods.image" mode="widthFix" class="img"></image>
		</view>
		<view class="goods-info">
			<view class="title">{{goods.title}}</view>
			<view class="dec">介绍: {{goods.describe}}</view>
			<view class="price-box">￥<text class="price">{{goods.price}}</text></view>
			<view class="text-box">
				<view class="item">销量: {{goods.sales}}</view>
				<view class="item">库存: {{goods.stock}}</view>
			</view>
		</view>
		<view class="other">
			<uni-row class="label-box">
				<uni-col :span="5">
					<view class="demo-uni-col dark">规格</view>
				</uni-col>
				<uni-col :span="19">
					<view class="demo-uni-col light">{{details.norms}}</view>
				</uni-col>
			</uni-row>
			<uni-row class="label-box">
				<uni-col :span="5">
					<view class="demo-uni-col dark">品牌</view>
				</uni-col>
				<uni-col :span="19">
					<view class="demo-uni-col light">{{details.producer}}</view>
				</uni-col>
			</uni-row>
			<uni-row class="label-box">
				<uni-col :span="5">
					<view class="demo-uni-col dark">服务</view>
				</uni-col>
				<uni-col :span="19">
					<view class="demo-uni-col light">品质保证、不满意包退</view>
				</uni-col>
			</uni-row>
		</view>
		<view class="goods-comment">
			<uni-list>
				<uni-list-item showArrow title="商品评价" rightText="查看" />
			</uni-list>
		</view>
		<view class="goods-detail">
			<view class="title">图文详情</view>
			<view class="content h5-content">
				<rich-text :nodes="nodes"></rich-text>
			</view>
		</view>
		<view class="goods-nav">
			<view class="icon-box icon-info-box" @click="cartClick">
				<uni-icons type="cart" size="22" class="cart"></uni-icons>
				<view class="text">购物车</view>
				<view class="info">{{goodsNumber}}</view>
			</view>
			<view class="icon-box" @click="starClick">
				<uni-icons :type="star?'star-filled':'star'" size="22" class="star"
					:color="star?'#ff557f':''"></uni-icons>
				<view class="text">收藏商品</view>
			</view>
			<button class="btn" @click="addCartGoods(goodsId)">加入购物车</button>
		</view>
	</view>
</template>

<script>
	import {
		mapActions,
		mapGetters,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				goods: {},
				details: '',
				nodes: '',
				goodsId: ''
			}
		},
		onLoad: function(option) {
			this.goodsId = option.goodsId
		},
		computed: {
			...mapState(['userInfo', 'collectList']),
			...mapGetters(['goodsNumber']),
			star() {
				if(this.collectList.length>0){
					let state = false
					this.collectList.forEach((item) => {
						if (item.user_id == this.userInfo.id && item.goods_id == this.goodsId) {
							state = true
						}
					})
					return state
				}else{
					return false
				}
			}
		},
		methods: {
			...mapActions(['addCartGoods', 'getCollectList','collectGoods','cancelCollectGoods']),
			async getGoodsDetail(goodsId) { // 获取商品详情
				const response = await this.$api.goods.getGoodsDetail(goodsId)
				if (response.data.code === 200) {
					this.goods = response.data.data
					this.details = this.goods.detail
					this.nodes = this.formatRichText(this.goods.detail.details)
				}
			},
			cartClick() { //购物车
				uni.switchTab({
					url: '/pages/cart/cart'
				})
			},
			starClick() { //收藏
				if (!this.star) {
					this.collectGoods(this.goodsId)
				} else {
					this.cancelCollectGoods(this.goodsId)
				}
			},
			formatRichText(html) { // 富文本内容格式化
				// #ifndef MP-WEIXIN
				return html
				// #endif
				// #ifdef MP-WEIXIN
				return html.replace(/\<img/gi, '<img style="width:100%;height:auto;float:left;"').replace(/\<ul/gi,
						'<ul style="font-size: 14px;line-height: 22px;margin-bottom:15px;padding:0;"')
					.replace(/\<li/gi, '<li style="list-style-type: none;"').replace(/\<p/gi,
						'<p style="overflow: hidden;"')
				// #endif
			}
		},
		onShow: function() {
			this.getGoodsDetail(this.goodsId)
			this.getCollectList()
		},
	}
</script>

<style lang="scss">
	.container {
		padding-bottom: 100upx;

		.goods-img {
			width: 100%;
			background-color: #fff;

			.img {
				width: 100%;
			}
		}

		.goods-info {
			background-color: #fff;
			padding: 0 20upx 20upx;

			.title {
				font-size: 30upx;
				font-weight: bold;
			}

			.dec {
				font-size: 24upx;
				line-height: 36upx;
				color: $uni-secondary-color;
				margin: 15upx 0 20upx;
			}

			.price-box {
				font-size: 22upx;
				color: #ff557f;

				.price {
					font-size: 35upx;
				}
			}

			.text-box {
				display: flex;
				align-items: center;
				margin-top: 15upx;

				.item {
					width: 50%;
					font-size: 24upx;
					color: $uni-base-color;
				}
			}
		}

		.other {
			background-color: #fff;
			font-size: 26upx;
			padding-top: 10upx;

			.uni-row {
				border-bottom: 1px solid $uni-border-2;
				height: 70upx;
				line-height: 70upx;

				&:last-child {
					border: 0;
				}

				.demo-uni-col {
					padding-left: 20upx;
				}
			}
		}

		.goods-comment {
			margin: 10upx 0;
		}

		.goods-detail {
			padding: 20upx 20upx 40upx;
			background-color: #fff;

			.title {
				text-align: center;
				font-size: 30upx;
				line-height: 90upx;
				position: relative;

				&::before {
					content: '';
					width: 110upx;
					height: 1upx;
					background-color: black;
					position: absolute;
					top: 46upx;
					left: calc(50% - 190upx);
				}

				&::after {
					content: '';
					width: 110upx;
					height: 1upx;
					background-color: black;
					position: absolute;
					top: 46upx;
					right: calc(50% - 190upx);
				}
			}

			.content {
				margin-top: 20upx;
			}

			/* #ifndef MP-WEIXIN */
			.h5-content {
				ul {
					font-size: 26upx;
					line-height: 45upx;
					margin-bottom: 20upx;
				}

				li {
					list-style-type: none;
				}

				img {
					width: 100%;
					height: auto;
					float: left;
				}

				p {
					overflow: hidden;
				}
			}

			/* #endif */
		}

		.goods-nav {
			position: fixed;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 90upx;
			background-color: #fff;
			display: flex;
			align-items: center;
			padding: 10upx 25upx 10upx 10upx;

			.icon-box {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				padding: 0 20upx;

				.text {
					font-size: 18upx;
					color: $uni-main-color;
					margin-top: -4upx;
				}
			}

			.btn {
				flex: 1;
				background-color: #349D7E;
				color: #fff;
				font-size: 28upx;
				border-radius: 50upx;
				height: 100%;
				margin-left: 15upx;
			}

		}

	}
</style>